div.order-block {
  left: 0;
  right: 0;
  bottom: 0;
  padding: 10px 16px;
  border-radius: 5px 5px 0 0;
  opacity: 1;
  visibility: visible;
  transform: translateY(100%);
  transition: bottom 0.3s, transform 0.3s;

  .button {
    padding: 11px 14px;
    height: auto;
    line-height: 120%;
  }

  .btn-basket {
    padding: 12px;
    height: 48px;

    svg {
      width: 20px;
      height: 20px;
    }
  }
}

span.order-block-price {
  margin-bottom: 2px;
  line-height: 120%;
}

span.order-block-sum {
  font-size: 12px;
}

span.order-block-title {
  max-width: none;
  color: #6C7684;
  font-size: 10px;
  line-height: 120%;
  margin: 0 0 2px;
  max-height: 24px;

  display: -webkit-box;

  overflow: hidden;
    text-overflow: ellipsis;
    display: -moz-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    line-clamp: 2;
    box-orient: vertical;
}

span.order-block-title + span.order-block-price {
  font-size: 12px;
}

.call-badge {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 8px 56px 8px 16px;
  background: #5EA9FF;
  color: #fff;
  z-index: 20;
  transform: translateY(100%);
  transition: transform 0.3s;

  &:hover {
    color: #fff;
    text-decoration: none;
  }

  @media (min-width: 768px) {
    display: none;
  }

  span {
    display: block;
    font-size: 15px;
    line-height: 120%;
    font-weight: 500;

    &:not(:first-child) {
      font-size: 12px;
      line-height: 160%;
      font-weight: 400;
    }
  }

  &__icon {
    width: 24px;
    height: 24px;
    position: absolute;
    top: 50%;
    right: 16px;
    transform: translateY(-50%);
  }
}

// div.order-block:has(+ .call-badge) {
//   bottom: 53px;
// }

.sticky-card-show {
  .call-badge {
    transform: translateY(0);
  }

  div.order-block {
    transform: translateY(0);
  }

  div.order-block:has(+ .call-badge) {
    bottom: 53px;
  }

  &:has(.order-block) {
    .footer {
      @media (max-width: 768px) {
        padding-bottom: 84px;
      }
    }
  }

  &:has(.call-badge) {
    .footer {
      @media (max-width: 768px) {
        padding-bottom: 64px;
      }
    }
  }

  &:has(.order-block + .call-badge) {
    .footer {
      @media (max-width: 768px) {
        padding-bottom: 140px;
      }
    }
  }
}
