.order-page__cloud-alert {
  padding: 16px;
  background-color: #FFEBEB;
  border-radius: 8px;
  display: flex;
  gap: 15px;
  width: 100%;
  margin: 15px 7px 25px;

  @media (max-width: 767px) {
    flex-direction: column;
    align-items: flex-start;
  }

  .image {
    width: 67px;
    height: 48px;
    background-image: url('../img/cloud-logo.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    flex-shrink: 0;
  }

  .text {
    margin: 0;
    padding-top: 5px;
    font-size: 12px;
    line-height: 160%;
    font-weight: 400;
    padding-left: 0;
  }
}

#bx-soa-properties {
  margin-left: -7px;
  margin-right: -7px;

  .bx-soa-section-title {
    padding-left: 7px;
    padding-right: 7px;
  }
}

.input-container {
  margin-bottom: 15px;
}

.bx-soa-section-content {
  .installation-note {
    font-size: 12px;
    line-height: 160%;
    font-weight: 400;
    color: #EF6461;
    margin-bottom: 0;
  }
}
